
import * as React from "react"
import { Link } from "gatsby"
import { graphql } from "gatsby"
import {Card, Button, ButtonGroup, CardGroup, Container} from 'react-bootstrap'
import Layout from "../../components/Layout/Layout"

const ArticlesIndex = ({data}) => {
	const pages = data.allMongodbSafeparasportDbArticles.nodes
	return (
		<Layout pageTitle="Articles Page">
			<div className="card_container">
				<h2 className="card_title">Current Articles Inventory</h2>
				{pages.map(page => <CardGroup style={{width: '100%'}}>
						<Card style={{width: '100%', height: 'fit-content'}}>
							<Card.Title>{page.title}</Card.Title>
							<ButtonGroup>
								<Button as={Link} to={page.slug} variant="primary">Read Article</Button>
								<Button as={Link} to={page.slug} variant="primary">Edit Article</Button>
							</ButtonGroup>
						</Card>
					</CardGroup>)}
			</div>
		</Layout>
	)
}

export default ArticlesIndex

export const query = graphql`
query{
	allMongodbSafeparasportDbArticles {
	  nodes {
		slug
		title
	  }
	}
  }
`
